.viedonban1 video {
    position: relative;
}
section.footer {
    width: 100%;
    position: relative;
    display: flex;
    margin: auto;
    justify-content: center;
    align-items: center;
    padding: 2rem 0;
}
.viedonban1 {
    position: absolute;
    height: 100%;
    width: 100%;
}
.viedonban1 video {
    object-fit: cover;
    position: relative;
}
.setCont.container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: max-content;
    padding: 2rem 0;
    align-items: center;
    margin: auto;
    row-gap: 2rem;
    z-index: 100;
}
.contentm.flex {
    justify-content: space-between;
    flex-direction: column;
    gap: 1rem;
}
.text {
    font-size: 13px;
    font-weight: 900;
    color: #000
}
h2.trec {
    color: #fff;
}

.main1{
    width: 100%;
    gap: 2rem;
    flex-direction: column;
    background-color: transparent;
    border: 2px solid #ccc;
    padding: 1rem 1rem;
    border-radius: 4rem;
} 
input{width: 100%;
padding: .5re .6rem;

}
.main1::placeholder{
    color: #fff;
}
.footer .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #0e0d0d6b;
    top: 0;
    bottom: 0;
    z-index: 1;
    mix-blend-mode: hard-light;
}
.inputdiv {
    display: flex;
    flex-direction: column;
}
.footerintroduction img {
    width: 100px;
}
.cardfooter.Card {
    justify-content: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
    background: #fff;
    margin: 27px;
    border-radius: 2rem;
    gap: 2rem;
}
.cardfooter.Card ul span {
    display: flex;
    margin-bottom: 10px;
}
.cardfooter.Card ul li a{
    display: flex;
    margin-bottom: 9px;
    gap:1rem
}