
:root {
    --primary--color: #1ec28b;
    --black--color: #000000;
    --secondary--color: #ffffff;
    --text-color: #73848c;
    --navy-color: #2b2945;
    --mustard-color: #ff8900;
    --yellow-color: #fdb913;
    --light-green-color: #b2f3de;
  }
.header{
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    width: 100%;
    z-index: 9999;
    background: #f7f7f7;
    box-shadow: 0 2px 4px rgb(206 212 217 / 94%);
}
@media screen and (max-width:767px) {
    
    .navbar{
        padding: 1rem;
        position: absolute;
        background-color: #fff;
        height: max-content;
        width: 80%;
        border-radius: 1rem;
        top: -500rem;
        left: 50%;
        transform: translate(-50%);
        z-index: 2000;
        box-shadow: 0 2px 4px rgba(2,15,20,0.94);
        transition: .5s ease-in-out;
    }
    .activanab{
        top: 7rem;
    }
    .navbar .navlist{

        flex-direction: column;
        justify-content: center;
        margin: auto;
        width: 100%;
       padding: 0.9rem 0;
    }
    li.navitem {
        padding: .5rem;
    }
    .navlink{
        color: var(--navy-color);
        font-size: .9rem;
        font-weight: 600;


    }
    .navlink:hover{
        color: var(--mustard-color);
    }
    .btn{
        margin-top: 1rem;
    }
    .coloseNavbar{
        position: absolute;
        top: 1rem;
        right: 1.5rem;
        color: var(--primary--color);
    }
    .coloseNavbar:hover{
        color: var(--mustard-color);
    }
}
.togglemenu >.icon{
 font-size: 25px;
 color: var(--primary--color);

}
.togglemenu>.icon:hover{
 color: var(--navy-color);
}