*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Josefin Sans", sans-serif;

}
:root {
  --primary--color: #1ec28b;
  --black--color: #000000;
  --secondary--color: #ffffff;
  --text-color: #73848c;
  --navy-color: #2b2945;
  --mustard-color: #ff8900;
  --yellow-color: #fdb913;
  --light-green-color: #b2f3de;
}
a{
  text-decoration: none;
}
li{list-style: none;}
.section{
  padding: 4re 2rem;
}
.container{
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}
.flex{
  display: flex;
  align-items: center;
}
.btn{
  padding: .6rem 1.5rem;
  background: var(--navy-color);
 
  border-radius: 3rem;
  border: none;
  outline: none;
cursor: pointer;
}
.grid{
  display: grid;
  align-items: center;
 
}
.btn a{
  color: #ffffff;
  font-weight: 400;
}
btn a:hover{
  background: var(--mustard-color);
}
img, video{
  width: 100%;
  height: auto;
}
body{
  background: #f7f7f7;
}
a.logo img {
  width: 200px;
}
.icon{
  cursor: pointer;
  font-size: 2rem;
}