:root {
    --primary--color: #1ec28b;
    --black--color: #000000;
    --secondary--color: #ffffff;
    --text-color: #73848c;
    --navy-color: #2b2945;
    --mustard-color: #ff8900;
    --yellow-color: #fdb913;
    --light-green-color: #b2f3de;
  }
.home{
    width: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
}
.home .overlay{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color:#f7f7f76b;
    top:0;
    bottom: 0;
    z-index: 1;
    mix-blend-mode: hard-light;
}
video{
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    bottom: 0;
    object-fit: cover;
}
.homecontant{
    width: 100%;
    height: max-content;
    padding: 8rem 1.5rem 3rem;
    display: flex;
    row-gap: 3rem;
    flex-direction: column;
    justify-content: center;
    align-items: stretch;
    margin: auto;
    z-index: 100;
color: #fff;

}
.textDiv{
    padding: 2rem 0rem;
}
.smalltext{
    font-size: 13px;
    text-transform: uppercase;
}
.hedingmai{
    position: relative;
    font-size: 1.8rem;
}

.card{
    position: relative;
    border-radius: 10px;
    background-color: #fff;
padding: 2rem 1rem;
gap: 1rem;
}
label{
    color: #000;
    display: block;
    padding-bottom: 10px;
    font-size: 13px;
    font-weight: 500;
}
.priceinput{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.total{
    color: #000;
}
.input{
    height: 40px;
    padding: 0 1rem;
    background-color: #f7f7f7;
    border-radius: 3rem;
}
input{
    width: 100%;
    background: none;
    outline: none;
    border: none;
    font-size: 13px;
    font-weight: 500;
}
::placeholder{
    font-size: 13px;
}
.input.flex .icon {
    font-size: 20px;
    color: #000;
}
.searchinput{
    position: absolute;
    background-color: var(--navy-color);
    max-width: max-content;
    bottom: -1rem;
    column-gap: .8rem;
    padding: 14px 1rem;
    border-radius: 3rem;
    left: 3%;
    transform: translate(50%);
    justify-content: center;

}